<template>
    <div class="consume">
        <index ref="child" options="options">
            <template slot="info">
                <p>您可对单笔满500元(含)以上的消费金额进行分期，并在约定期限内偿还。</p>
            </template>
            <div class="field-action" slot="btn">
                <mt-button type="default" size="large" @click="doApply">我要申请</mt-button>
            </div>
            <template slot="process">
                <p>
                    办理流程：
                </p>
                <p>
                    刷卡消费→持卡人申请（账单生成之前）→分期偿还
                </p>
            </template>
            <template slot="warn">
                <p>
                    温馨提示：
                </p>
                <p>
                    1、您可选择3、6、9、12、18、24期进行惠消费分期，一个月为一期；
                </p>
                <p>
                    2、惠消费撤销可在申请完成后至账单日之前操作。
                </p>
            </template>
        </index>
    </div>
</template>

<script>
    import Index from '../public';
    export default {
        name: 'consume',
        components: {
            Index
        },
        data() {
            return {
                msg: ''
            };
        },
        methods: {
            doApply() {
                let _data = Object.assign({}, this.$refs.child.formData);
                if (this.$validate.isEmpty(_data.account)) {
                    this.$msg('请选择信用卡卡号');
                    return false;
                }
                this.$router.push({ name: 'CreditConsumeList' });
            }
        }
    };

</script>


