<template>
    <div class="credit">
        <div class="hd">
            <!--<div class="login-wrap">
                <div class="hd-desc" v-text="flag.desc"></div>
                <button class="hd-btn" v-text="flag.btn"></button>
            </div>-->
            <div class="credit-card">
                <div class="head">
                    安徽农金621345****1234 >
                </div>
                <div class="content">
                    <div class="wrap-top">
                        <div class="wrap">
                            <div class="wrap-left">本期应还（元）</div>
                            <div class="wrap-right"> 还款日：6月27日</div>
                        </div>
                        <div class="wrap">
                            <div class="wrap-left amount">2500.00</div>
                            <div class="wrap-right bill-btn">
                                <mt-button @click="bill" class="query-bill" type="primary" size="small" plain>查账单 ></mt-button>
                            </div>
                        </div>
                        <div class="wrap">
                            <div class="wrap-left rest-amount">剩余未还 134389.00元
                            </div>
                            <div class="wrap-right date-off"> 已逾期 </div>
                        </div>
                    </div>

                    <div class="wrap">
                        <div class="wrap-left">
                            剩余额度 200，000.00元
                        </div>
                        <div class="wrap-right">
                            信用额度 500，000.00元
                        </div>
                    </div>
                </div>
                <div class="foot">
                    <mt-button size="large" @click="repayment" type="default">我要还款</mt-button>
                </div>
            </div>
        </div>
        <div class="staging-product">
            <p class="product-title">乐享分期 消费无忧</p>
            <div class="product-main">
                <span v-for="item in module" class="products" @click="doHref(item)">
                    <img :src="pic" width="26" height="26">
                    <p  v-text="item.name"></p>
                    <p  v-text="item.desc"></p>
                </span>
            </div>
        </div>
        <div class="banner">
            <mt-swipe :auto="0">
                <mt-swipe-item>
                    <img src="../../assets/img/banner.png" width="100%" height="240">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="../../assets/img/banner.png" width="100%" height="240">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="../../assets/img/banner.png" width="100%" height="240">
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <div class="more">
            <span v-for="item in moreModule" class="products">
                    <img :src="pic" width="26" height="26">
                    <p v-text="item.name"></p>
                </span>
        </div>

    </div>
</template>

<script>
    import Pic from '../../assets/img/default.png';
    import Vue from 'vue';
    import { Swipe, SwipeItem } from 'mint-ui-rem-dpr';

    Vue.component(Swipe.name, Swipe);
    Vue.component(SwipeItem.name, SwipeItem);
    export default {
        name: 'credit',
        data() {
            return {
                status: false,
                pic: Pic,
                flag: {
                    desc: '账单总览 查询额度',
                    btn: '登录'
                },
                module: [
                    { name: '惠消费', desc: '灵活分期', href: 'CreditConsume' },
                    { name: '惠分单', desc: '账单分期', href: 'CreditConsume' },
                    { name: '惠提现', desc: '现金分期', href: 'CreditConsume' },
                    { name: '惠借钱', desc: '大额分期', href: 'CreditConsume' }
                ],
                moreModule: [
                    { name: '预借现金', href: 'CreditConsume' },
                    { name: '我的积分', href: 'CreditConsume' },
                    { name: '申请进度', href: 'CreditConsume' },
                    { name: '一键激活', href: 'CreditConsume' },
                    { name: '快速挂失', href: 'CreditConsume' },
                    { name: '闪电补卡', href: 'CreditConsume' }
                ]
            };
        },
        methods: {
            doHref(item) {
                this.$router.push({ name: item.href });
            },
            bill() {
                this.$router.push({ name: 'CreditBill' });
            },
            repayment() {
                this.$router.push({ name: 'CreditRepayment' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .hd {
        position: relative;
        width: 100%;
        height: auto;
        /*text-align: center;*/
        /* 登录/添加信用卡 */
        .login-wrap {
            padding: 60px 0;
            .hd-desc {
                color: #fff;
            }
            .hd-btn {
                margin-top: 20px;
                padding: 10px 60px;
                color: #fff;
                background-color: #318dd2;
                border: 1px solid #fff;
                border-radius: 15px;
            }
        }

        /*  信用卡 */
        .credit-card {
            margin: 30px;
            background-color: #fff;
            color: #999;
            @include border-radius( $dialog-border-radius);

            .head {
                position: relative;
                padding: 0 20px;
                height: 60px;
                line-height: 60px;

                @include mini-border(bottom);
            }

            .content {
                padding: 10px 20px;

                .wrap-top {
                    position: relative;
                    @include mini-border(bottom);

                    .wrap+.wrap {
                        margin-top: 15px;
                    }
                }

                .wrap {
                    padding: 10px 0;
                    display: flex;
                    display: -webkit-box;
                    display: -ms-flexbox;

                    .amount {
                        font-size: 22px;
                        font-weight: bold;
                        color: #5a5a5a;
                    }

                    .rest-amount {
                        color: #ea7f31;
                    }

                    .bill-btn {
                        margin-top: -8px;
                    }

                    .date-off {
                        color: red;
                        margin-right: 12px;
                    }

                    .wrap-left {
                        flex: 1;
                        text-align: left;
                    }

                    .wrap-right {
                        text-align: right;

                        .query-bill {
                            border-radius: 16px;
                            height: 56px;
                        }
                    }
                }
            }

            .foot {
                padding: 0px;

                .mint-button {
                    font-size: 14px;

                    @include border-radius(0px 0px $dialog-border-radius $dialog-border-radius);
                }
            }
        }
    }

    .staging-product {
        position: relative;
        @include mini-border(top,
        #d9d9d9,
        after);
        .product-title {
            padding: 20px 0;
            text-align: center;
            background-color: #fafafa;
        }

        .product-main {
            position: relative;
            background-color: #fff;
            @include mini-border(top,
            #999,
            after);
            @include mini-border(bottom,
            #999,
            before);

            .products+.products {
                @include mini-border(left,
                #d9d9d9,
                before);
            }
        }
    }

    .products {
        position: relative;
        padding: 20px 0;
        text-align: center;
        display: inline-block;
        width: 24.37%;

        img {
            border-radius: 50%;
            margin-bottom: 15px;
        }

        p {
            font-size: 12px;
        }
    }

    .more {
        position: relative;
        background-color: #fff;
        @include mini-border(top,
        #999,
        after);
        @include mini-border(bottom,
        #999,
        before);
    }

    .banner {
        margin: 20px 0;
        .mint-swipe {
            height: 240px;
            img {
                width: 100%;
                height: 100%;
            }
        }
    }
</style>


