<template>
    <div class="public-submit">
        <div class="field-part">
            <mt-cell title="交易日期" value="2017年6月30日"></mt-cell>
            <mt-cell title="交易金额" value="550.00元"></mt-cell>
        </div>

        <div class="field-part">
            <mt-cell class="mint-select" title="分期期数">
                <select class="mint-field-core" v-model="num">
                    <option v-for="option in options" key="item.id" :value="option.code">
                        {{option.title}}
                    </option>
                </select>
            </mt-cell>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'public-submit',
        data() {
            return {
                num: '',
                options: [
                    { code: '1', title: '1期' },
                    { code: '2', title: '2期' },
                    { code: '3', title: '3期' }
                ]
            };
        }
    };

</script>

<style lang="scss" scoped>

</style>


