<template>
    <div class="public-index">
        <apply ref="child"></apply>
        <div class="field-action">
            <mt-button type="default" size="large" @click="confirm">确认申请</mt-button>
        </div>
    </div>
</template>

<script>
    import Apply from '../public/apply';
    export default {
        name: 'public-index',
        components: {
            Apply
        },
        data() {
            return {
                msg: ''
            };
        },
        methods: {
            confirm() {
                if(this.$validate.isEmpty(this.$refs.child.password)) {
                    this.$msg('请输入交易密码！');
                    return false;
                }
            }
        }
    };

</script>

<style lang="scss" scoped>

</style>


