<template>
    <div class="bill">
        <div class="field-part">
            <cell title="信用卡卡号" @click.native="showCreditAccount()" v-model="creditData.account" isLink select></cell>
            <selector-payment-account ref="creditAccount" :options=" creditAccount "></selector-payment-account>
            <div class="panel">
                <div class="panel-top">
                    <p>剩余未还（元）</p>
                    <p class="amount">12,390.97</p>
                    <p>最终还款日：</p>
                </div>
                <div class="panel-bottom">
                    <p class="now">本期应还</p>
                    <p class="low">最低还款</p>
                </div>
            </div>
        </div>
        <div class="field-part">
            <cell title="借记卡卡号" @click.native="showDebitAccount()" v-model="debitData.account" isLink select></cell>
            <selector-payment-account ref="debitAccount" :options=" debitAccount "></selector-payment-account>
            <mt-cell class="field-self-style" title="还款金额">
                <input @focus="amountFocus()" @blur="amountBlur()" class="mint-field-core" type="text" placeholder="请输入还款金额">
            </mt-cell>
            <template v-if="showAmount">
                <mt-field label="全额还款"></mt-field>
                <mt-field label="最低还款"></mt-field>
            </template>
        </div>
    </div>
</template>

<script>
    import * as service from '@/services/payment';
    export default {
        name: 'bill',
        data() {
            return {
                creditData: {
                    account: ''
                },
                debitData: {
                    account: '',
                    amount: ''
                },
                creditAccount: {
                    title: '选择信用卡',
                    type: 'credit',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                debitAccount: {
                    title: '选择借记卡',
                    type: 'payment',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                showAmount: false
            };
        },
        created() {
            service.getAccountList()
                .then((res) => {
                    this.creditAccount.accountList = res.data;
                    this.debitAccount.accountList = res.data;
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showCreditAccount() {
                this.$refs.creditAccount.show().then((data) => {
                    this.creditAccount.checked = data.item;
                    this.creditData.account = this.creditAccount.checked.account;
                }, (data) => {
                });
            },
            showDebitAccount() {
                this.$refs.debitAccount.show().then((data) => {
                    this.debitAccount.checked = data.item;
                    this.debitData.account = this.debitAccount.checked.account;
                }, (data) => {
                });
            },
            amountFocus() {
                this.showAmount = true;
            },
            amountBlur() {
                this.showAmount = false;
            }
        }
    };

</script>
<style lang="scss">
    .field-self-style {
        .mint-cell-title {
            width: 210px;
            flex: none;
        }

        .mint-cell-value {
            flex: 1;
            color: inherit;
            display: flex;
        }

        .mint-field-core {
            appearance: none;
            border-radius: 0;
            border: 0;
            flex: 1;
            outline: 0;
            line-height: 1.6;
            font-size: inherit;
            width: 100%;
        }
    }
</style>
<style lang="scss" scoped>
    .panel {
        text-align: center;
        padding: 50px 20px;
        margin-bottom: 30px;
        background-color: #e0e0e0;

        .panel-top {
            position: relative;
            padding-bottom: 15px;
            @include mini-border(bottom, #a9acb1);

            p+p {
                margin-top: 20px;
            }

            .amount {
                color: #ea7f31;
                font-size: 18px;
            }
        }

        .panel-bottom {
            display: flex;
            display: -webkit-box;
            display: -ms-flexbox;
            padding-top: 15px;

            p {
                flex: 1;
            }

            .now {
                text-align: left;
            }

            .low {
                text-align: right;
            }
        }
    }
</style>


