<template>
    <div class="bill">
        <div class="field-part">
            <cell title="信用卡卡号" @click.native="showAccount()" v-model="formData.account" isLink select></cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>
        <div class="field-top">
            <div class="left">
                账单日：
            </div>
            <div class="right">
                还款日：
            </div>
        </div>
        <div class="field-cell" @click="currentBill">
            本期账单明细 >
        </div>
        <div class="field-part">
            <mt-cell title="未出账单" :to="{name: 'CreditBillOut'}" is-link></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="历史账单" :to="{name: 'CreditBillHistory'}" is-link></mt-cell>
        </div>
    </div>
</template>

<script>
    import * as service from '@/services/payment';
    export default {
        name: 'bill',
        data() {
            return {
                formData: {
                    account: ''
                },
                selectorPaymentAccount: {
                    title: '选择付款账号',
                    type: 'credit',
                    checked: {},
                    showAddBtn: false,
                    showWarn: false,
                    accountList: []
                }
            };
        },
        created() {
            service.getAccountList()
                .then((res) => {
                    this.selectorPaymentAccount.accountList = res.data;
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.account;
                }, (data) => {
                });
            },
            currentBill() {
                this.$router.push({ name: 'CreditBillCurrent' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-top {
        position: relative;
        display: flex;
        display: -webkit-box;
        display: -ms-flexbox;
        padding: 20px;
        background-color: #fafafa;
        @include mini-border(top);

        .left {
            flex: 1;
        }
    }

    .field-cell {
        position: relative;
        padding: 20px;
        margin-bottom: 15px;
        color: #26a2ff;
        background-color: #fff;
        text-align: center;
        @include mini-border(top, #999, after);
        @include mini-border(bottom, #999, before);
    }
</style>


