<template>
    <div class="current">
        <div v-if="!code">
            <div v-if="billDate || refundDate" class="field-top">
                <div class="left">
                    账单日：{{billDate}}
                </div>
                <div class="right">
                    还款日：{{refundDate}}
                </div>
            </div>
            <div v-if="totalAmount" class="total-amount">
                <p>账单总额（元）</p>
                <p class="amount" v-text="totalAmount"></p>
            </div>
            <div class="field-part">
                <cell v-for="(bill, index) in billList" key="index" :title="bill.remark" label="尾号 1234" :value="bill.amount" :desc="bill.date"></cell>
            </div>
            <slot name="btn"></slot>
        </div>
        <div class="no-data" v-else>
            <p>未查询到明细记录</p>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/credit';
    export default {
        name: 'current',
        props: ['option'],
        computed: {
            code() {
                return this.option.code;
            },
            billDate() {
                return this.option.bill_date || false;
            },
            refundDate() {
                return this.option.refund_date || false;
            },
            totalAmount() {
                return this.option.total_amount || false;
            },
            billList() {
                return this.option.list;
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-top {
        position: relative;
        display: flex;
        display: -webkit-box;
        display: -ms-flexbox;
        padding: 20px;
        background-color: #fafafa;
        @include mini-border(top);

        .left {
            flex: 1;
        }
    }

    .total-amount {
        text-align: center;
        padding: 50px 0;
        margin-bottom: 15px;
        background-color: #e0e0e0;

        p+P {
            margin-top: 15px;
        }

        .amount {
            color: #ea7f31;
            font-size: 18px;
        }
    }

    .bottom-btn {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        display: -webkit-box;
        display: -ms-flexbox;
        background-color: #fafafa;
        text-align: center;
        height: 96px;
        line-height: 96px;
        @include mini-border(top);

        div {
            position: relative;
            flex: 1;
            line-height: 96px;

            .btn-desc {
                font-size: 12px;
            }
        }

        div+div {
            @include mini-border(left);
        }
    }

    .no-data {
        text-align: center;
        margin-top: 30px;
    }
</style>


