<template>
    <div class="public-index">
        <div class="field-part">
            <cell title="信用卡卡号" @click.native="showAccount()" v-model="formData.account" isLink select></cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <div class="panel info">
                <slot name="info"></slot>
            </div>
        </div>

        <slot name="btn"></slot>

        <div class="panel">
            <slot name="process"></slot>
            <div class="warn">
                <slot name="warn"></slot>
            </div>
        </div>
    </div>
</template>

<script>
    import * as service from '@/services/payment';
    export default {
        name: 'public-index',
        data() {
            return {
                formData: {
                    account: '',
                    code: ''
                },
                selectorPaymentAccount: {
                    title: '选择付款账号',
                    type: 'credit',
                    checked: {},
                    showAddBtn: false,
                    showWarn: false,
                    accountList: []
                }
            };
        },
        created() {
            service.getAccountList()
                .then((res) => {
                    this.selectorPaymentAccount.accountList = res.data;
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.account;
                }, (data) => {
                });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .panel {
        position: relative;
        margin: 20px 0;
        padding: 20px;
        @include mini-border(top, #ccc, after);

        p {
            line-height: 180%;
        }

        .warn {
            margin-top: 40px;
        }
    }

    .info {
        background-color: #fff;
        @include mini-border(bottom, #ccc, before);
    }
</style>


