<template>
    <div class="current">
        <bill :option="options"></bill>
    </div>
</template>

<script>
    import Bill from '../bill';
    import * as services from '@/services/credit';
    export default {
        name: 'current',
        components: {
            Bill
        },
        data() {
            return {
                options: {
                    code: true,
                    bill_date: '',
                    refund_date: '',
                    total_amount: '',
                    list: null
                }
            };
        },
        created() {
            services.queryCurrentBill({ showLoading: '请等待...' })
                .then(res => {
                    let _data = res.data;
                    if (_data.code !== 0) {
                        this.options.code = false;
                        this.options.bill_date = _data.bill_date;
                        this.options.refund_date = _data.refund_date;
                        this.options.total_amount = _data.total_amount;
                        this.options.list = _data.list;
                    }
                });
        }
    };

</script>

<style lang="scss" scoped>

</style>


