<template>
    <div class="public-index">
       <list @click.native="doHref()"></list>
    </div>
</template>

<script>
    import List from '../public/list';
    export default {
        name: 'public-index',
        components: {
            List
        },
        data() {
            return {
                list: null
            };
        },
        methods: {
            doHref() {
                this.$router.push({ name: 'CreditConsumeSubmit' });
            }
        }
    };

</script>


