<template>
    <div class="public-index">
        <submit ref="child"></submit>
        <div class="agree">
            <input type="checkbox" class="checkbox" v-model="agree"> 我已阅读并同意
            <span class="item" @click="showAgreement">惠消费分期协议</span>
        </div>
        <div class="field-action" slot="btn">
            <mt-button :disabled="!agree" type="default" size="large" @click="submit">提交申请</mt-button>
        </div>
        <dialog-agreement ref="agreement" options="agreeOptions"></dialog-agreement>

    </div>
</template>

<script>
    import Submit from '../public/submit';
    import Html from './agreement';
    export default {
        name: 'public-index',
        components: {
            Submit
        },
        data() {
            return {
                agree: false,
                agreeOptions: {
                    title: '惠消费分期协议',
                    btnConfirmText: '同意协议并提交惠消费申请'
                }
            };
        },
        methods: {
            showAgreement() {
                this.$refs.agreement.show(Html)
                    .then(data => {
                        this.agree = true;
                    }, data => {
                        console.log(data);
                    });
            },
            submit() {
                if (this.$validate.isEmpty(this.$refs.child.num)) {
                    this.$msg('请选择分期数');
                    return false;
                }
                this.$router.push({ name: 'CreditConsumeApply' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .agree {
        padding: 25px 0 5px 0;
        text-align: center;

        .checkbox {
            display: inline-block;
            vertical-align: middle;
        }

        .item {
            color: #26a2ff;
        }
    }
</style>


