<template>
    <div class="current">
        <bill :option="options">
            <div slot="btn">
                <div class="bottom-btn">
                    <div @click="doHref('CreditConsume')">
                        <p>惠消费 <span class="btn-desc"> · 灵活分期</span></p>
                    </div>
                </div>
            </div>
        </bill>
    </div>
</template>

<script>
    import Bill from '../bill';
    import * as services from '@/services/credit';
    export default {
        name: 'current',
        components: {
            Bill
        },
        data() {
            return {
                options: {
                    code: true,
                    list: null
                }
            };
        },
        created() {
            services.queryCurrentBill({ showLoading: '请等待...' })
                .then(res => {
                    let _data = res.data;
                    if (_data.code !== 0) {
                        this.options.code = false;
                        this.options.list = _data.list;
                    }
                });
        },
        methods: {
            doHref(name) {
                this.$router.push({ name: name });
            }
        }
    };

</script>

<style lang="scss" scoped>

</style>


